import React, { useRef, Suspense, useEffect, useState } from "react";
import { Canvas, useFrame, useLoader } from "@react-three/fiber";

import * as THREE from "three";
import threesixty from "./360.jpg";
import { Sky, Stars, Text, Environment } from "@react-three/drei";
import { CameraShake } from "@react-three/drei";
import "./style.css";

const config = {
  maxYaw: 0.1, // Max amount camera can yaw in either direction
  maxPitch: 0.1, // Max amount camera can pitch in either direction
  maxRoll: 0.1, // Max amount camera can roll in either direction
  yawFrequency: 0.1, // Frequency of the the yaw rotation
  pitchFrequency: 0.1, // Frequency of the pitch rotation
  rollFrequency: 0.1, // Frequency of the roll rotation
  intensity: 1, // initial intensity of the shake
  decay: false, // should the intensity decay over time
  decayRate: 0.65, // if decay = true this is the rate at which intensity will reduce at
  controls: undefined, // if using orbit controls, pass a ref here so we can update the rotation
};

function Box(props) {
  const mesh = useRef();
  useFrame(() => (mesh.current.rotation.x = mesh.current.rotation.y += 0.03));
  const base = new THREE.TextureLoader().load(threesixty);

  return (
    <mesh {...props} ref={mesh}>
      <circleGeometry args={[10, 32]} />
      <meshPhongMaterial attach="material" map={base} />
    </mesh>
  );
}

export default function App() {
  return (
    <Canvas camera={{ position: [0, 0, 35] }}>
      <pointLight position={[40, 40, 40]} />
      <Box />
      <Stars
        radius={100}
        depth={30}
        count={5000}
        factor={5}
        saturation={0}
        fade
      />
      <CameraShake {...config} />
      <Text color="white" anchorX="center" anchorY="middle">
        Headsgone
      </Text>
    </Canvas>
  );
}
